import React, { Component, lazy, Suspense } from "react";

import BootstrapTable from "react-bootstrap-table-next";

import Swal from "sweetalert2";
import { getListKirimMasak } from "../actions/kirimMasakAction";
import { connect } from "react-redux";
import { getUUID } from "../actions/fakturAction";
import {
  finishSend,
  getBruto,
  getNetto,
  getQty,
  progressSend,
} from "../actions/LogicAction";
import Axios from "axios";
import { getListTukang } from "../actions/listTukang";
import PrintNota from "./Component/PrintNota";
import { warning } from "../Alert";
import { reset } from "redux-form";
import { deleteIndex } from "../deleteLocalStorage";
import ReactTooltip from "react-tooltip";
import Skeleton from "react-loading-skeleton";
import { showModal } from "../actions/ModalAction";
import HeaderCard from "../HeaderCard";

const FormKirimMasak = lazy(() => import("./Component/FormKirimMasak"));
const FormKirimMasakBarang = lazy(() =>
  import("./Component/FormKirimMasakBarang")
);
const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);
const maptostate = (state) => {
  return {
    listBarang: state.provinsi.kirimMasak,
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    sumBruto: state.perhitungan.sumBruto,
    sumNetto: state.perhitungan.sumNetto,
    sumQty: state.perhitungan.sumQty,
    onSend: state.provinsi.onSend,
  };
};
const server = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
const serverMaster = process.env.REACT_APP_BACKEND_URL;
class KirimMasak extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: [],
      response: "",
      tanggal: "",
      tukang: "DEFAULT|DEFAULT",
      listTukang: [],
      columns: [
        {
          dataField: "kodeJenis",
          text: "Kode Jenis",
          footer: "",
        },
        {
          dataField: "kadarTransaksi",
          text: "Kadar Cetak(%)",
          footer: "",
        },
        {
          dataField: "bruto",
          text: "Bruto(Gr)",
          footer: (columnData) =>
            columnData
              .reduce((acc, item) => parseFloat(acc) + parseFloat(item), 0)
              .toFixed(3),
        },
        {
          dataField: "netto",
          text: "Netto(Gr)",
          footer: (columnData) =>
            columnData
              .reduce((acc, item) => parseFloat(acc) + parseFloat(item), 0)
              .toFixed(3),
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() =>
                    deleteIndex(
                      "kirimMasak",
                      rowIndex,
                      this.props.dispatch,
                      getListKirimMasak()
                    )
                  }
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
                {/* <button
                  data-tip
                  data-for="edit"
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => console.log("EDIT")}
                >
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                  <i className="fa fa-edit mr-1"></i>
                </button> */}
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getListKirimMasak());
    this.props.dispatch(getUUID());
    this.props.dispatch(getBruto("kirimMasak"));
    this.props.dispatch(getNetto("kirimMasak"));
    this.props.dispatch(getQty("kirimMasak"));
    this.panggilTukang();
  }

  panggilTukang() {
    Axios.get(serverMaster + "storages2?tipeData=TUKANG")
      .then((res) =>
        this.setState({
          listTukang: res.data,
        })
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Sepertinya ada kendala, silahkan cek koneksi",
          showConfirmButton: false,
          timer: 1500,
        }).then(() => console.log(err))
      );
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  handleSubmit(data) {
    console.log(data.bruto);
    if (data.kodeBarang === undefined) {
      warning("Mohon pilih kode barang");
      return false;
    } else if (data.bruto === undefined) {
      warning("Mohon isi Bruto");
      return false;
    }
    var supp = data.kodeBarang
      ? data.kodeBarang
      : "DEFAULT|DEFAULT|DEFAULT|DEFAULT";
    var split = supp.split("|");
    var nama = {
      noFaktur: this.props.noFaktur.toUpperCase(),
      netto: parseFloat(data.netto),
      bruto: parseFloat(data.bruto),
      rp: 0,
      kadarTransaksi: parseFloat(data.kadarTransaksi),
      nettoDiscGlobalExt: 0,
      keterangan: split[3],
      pkg: 0,
      gross: 0,
      p: 0,
      kodeKategori: "-",
      qty: 1,
      k: 0,
      kadarCetak: parseFloat(data.kadar),
      noBarcode: "-",
      kodeJenis: split[0].toUpperCase(),
      discGlobalGr: 0,
      nettoDiscGlobal: 0,
      discGlobalPr: 0,
    };
    var array = JSON.parse(localStorage.getItem("kirimMasak")) || [];
    array.push(nama);
    localStorage.setItem("kirimMasak", JSON.stringify(array));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    })
      .then(() => this.props.dispatch(getNetto("kirimMasak")))
      .then(() => this.props.dispatch(getBruto("kirimMasak")))
      .then(() => this.props.dispatch(getQty("kirimMasak")));
    this.props.dispatch(getListKirimMasak());
    this.props.dispatch(reset("FormKirimMasakBarang"));
  }

  kirimMasak() {
    if (this.state.tukang === "DEFAULT|DEFAULT") {
      warning("Mohon isi data tukang");
      return false;
    } else if (this.state.tanggal === "") {
      warning("Mohon isi tanggal");
      return false;
    } else if (localStorage.getItem("kirimMasak") === null) {
      warning("Barang masih kosong, Mohon tambahkan barang");
      return false;
    }
    var supp = this.state.tukang ? this.state.tukang : "DEFAULT|DEFAULT";
    var split = supp.split("|");
    var simpan = {
      noFaktur: this.props.noFaktur.toUpperCase(),
      noBon: this.props.noFaktur.toUpperCase(),
      tanggal: this.state.tanggal.toUpperCase(),
      kodeTukang: split[0].toUpperCase(),
      namaTukang: split[1].toUpperCase(),
      kodeLokasi: split[0].toUpperCase(),
      barangDetails: JSON.parse(localStorage.getItem("kirimMasak")),
      footerItem: {
        totalNetto: this.props.sumNetto,
        totalBruto: this.props.sumBruto,
        totalNettoExt: 0,
        totalP: 0,
        totalGross: 0,
        totalQty: this.props.sumQty,
        totalpkg: 0,
        totalRp: 0,
        totalK: 0,
      },
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    console.log(JSON.stringify(simpan));
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("kirimMasak"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        i++,
        ticket.kodeJenis,
        ticket.bruto,
        ticket.kadarTransaksi,
        ticket.netto,
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    console.log(JSON.stringify(tableRows));
    var columTable = ["No", "Kode Jenis", "Bruto", "Harga (%)", "Netto"];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(server + "item/dc/kirimmasak", simpan)
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Transaksi Berhasil",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => this.props.dispatch(finishSend()))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() =>
            PrintNota(
              "Tanggal",
              this.state.tanggal.toUpperCase(),
              "Kode Tukang",
              split[0],
              "No Faktur",
              this.props.noFaktur.toUpperCase(),
              "",
              "",
              "ADMIN",
              this.state.tanggal.toUpperCase(),
              "",
              columTable,
              "BUKTI KIRIM MASAK",
              tableRows
            )
          )
          .then(() => localStorage.removeItem("kirimMasak"))
          .then(() => this.props.dispatch(getListKirimMasak()))
          .then(() =>
            this.setState({
              tanggal: "",
              tukang: "",
            })
          )
          .then(() => this.props.dispatch(getUUID()))
          .then(() => this.props.dispatch(reset("FormKirimMasakBarang")))
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Sepertinya ada kendala, silahkan cek koneksi",
          showConfirmButton: false,
          timer: 1500,
        }).then(() => console.log(err))
      );
  }

  tambahTukang(data) {
    var simpan = {
      tipeData: "TUKANG",
      kodeLokasi: data.kodeLokasi.toUpperCase(),
      kodeExt: data.kodeLokasi.toUpperCase(),
      statusAktif: "AKTIF",
      namaLokasi: data.namaLokasi.toUpperCase(),
    };
    Axios.post(serverMaster + "storage", simpan)
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Tambah Data Berhasil",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => this.props.dispatch(getListTukang()))
          .then(() => this.panggilTukang())
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Sepertinya ada kendala, silahkan cek koneksi",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => console.log(err))
          .then(() => this.props.dispatch(finishSend()))
      );
  }

  kirimModal() {
    this.props.dispatch(showModal());
    this.setState({
      modalKirimMasak: true,
    });
  }
  tukangModal() {
    this.props.dispatch(showModal());
    this.setState({
      modalKirimMasak: false,
    });
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Kirim Masak" />
              <div className="card-body">
                <div className="col-lg-12">
                  <div className="row">
                    <div className="col-md-3 col-lg-3">
                      <label htmlFor="">Tanggal</label>
                      <input
                        name="tanggal"
                        id="tanggal"
                        type="date"
                        className="form-control"
                        onChange={this.inputChange("tanggal")}
                        value={this.state.tanggal}
                      ></input>
                    </div>
                    <div className="col-md-4 col-lg-3">
                      <label htmlFor="">Tukang</label>
                      <select
                        name="tukang"
                        id="tukang"
                        className="form-control"
                        onChange={this.inputChange("tukang")}
                        value={this.state.tukang}
                      >
                        <option value="DEFAULT|DEFAULT" key="DEFAULT">
                          SILAHKAN PILIH
                        </option>
                        {this.state.listTukang.map((list) => (
                          <option
                            value={list.kodeLokasi + "|" + list.namaLokasi}
                            key={list.kodeLokasi}
                          >
                            {list.namaLokasi}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div
                      className="col-lg-1 col-md-1 no-gutters form-group"
                      style={{ marginLeft: -30 }}
                    >
                      <label htmlFor="" className="text-white">
                        .
                      </label>
                      <button
                        className="btn btn-primary form-control"
                        onClick={() => this.tukangModal()}
                      >
                        +
                      </button>
                    </div>
                    <div className="col-md-5 col-lg-5"></div>
                  </div>
                </div>
              </div>
              <div className="col-lg-12 mb-1">
                <div className="col-lg-12 mb-3">
                  <div className="row">
                    <div className="col-lg-6">
                      <div className="text-left">
                        <button
                          disabled={this.props.onSend}
                          className="btn btn-primary"
                          onClick={() => this.kirimMasak()}
                        >
                          {this.props.onSend ? (
                            <span
                              className="spinner-border spinner-border-sm mr-3"
                              aria-hidden="true"
                            ></span>
                          ) : null}
                          Simpan
                          <i className="fa fa-paper-plane ml-3"></i>
                        </button>
                      </div>
                    </div>
                    <div className="col-lg-6">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.kirimModal()}
                        >
                          Tambah Data
                          <i className="fa fa-plus ml-3"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-lg-12 mb-3">
                <div className="col-lg-12">
                  {this.props.listBarang ? (
                    <BootstrapTable
                      keyField="kodeJenis"
                      data={this.props.listBarang}
                      columns={this.state.columns}
                      bordered={true}
                      headerClasses="header-table"
                    />
                  ) : (
                    <BootstrapTable
                      keyField="kodeJenis"
                      data={[]}
                      columns={this.state.columns}
                      bordered={true}
                      headerClasses="header-table"
                      noDataIndication="Tidak Ada Data"
                    />
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
        <ModalMasterData
          content={
            <Suspense
              fallback={<Skeleton width={"100%"} height={50} count={5} />}
            >
              {this.state.modalKirimMasak ? (
                <FormKirimMasakBarang
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              ) : (
                <FormKirimMasak onSubmit={(data) => this.tambahTukang(data)} />
              )}
            </Suspense>
          }
          title="Tambah Data Barang"
        />
      </div>
    );
  }
}

export default connect(maptostate, null)(KirimMasak);
